import {Component, OnInit} from '@angular/core';
import {AngularFireAuth} from "angularfire2/auth";
import {AngularFirestore, AngularFirestoreDocument} from "angularfire2/firestore";
import {NgForm} from "@angular/forms";
import {Router, RouterModule} from "@angular/router";
import {UserService} from "../user.service";
import {User} from "firebase";

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  auth: AngularFireAuth;
  firestore: AngularFirestore;
  router : Router;
  userService : UserService;
  isLoginPage = true;
  constructor(auth: AngularFireAuth, firestore: AngularFirestore,router : Router, userService :UserService) {
    this.auth = auth;
    this.firestore = firestore;
    this.router = router;
    this.userService = userService;
  }

  ngOnInit() {


  }

  login(loginForm: NgForm) {
    let email = loginForm.value.userEmail;
    let password = loginForm.value.userPassword;

    this.auth.auth.signInWithEmailAndPassword(email, password)
      .then(
        user => {
          let uid = this.auth.auth.currentUser.uid;
          this.firestore.doc('instructors/' + uid).valueChanges().subscribe(data => {
            console.log(data);
            this.userService.setUserLoggedIn();
            this.router.navigate(['/sections'], { queryParams: { returnUrl: '' }});


          });

        }
      ).catch(error => {
      console.log(error);
    })


  }

}



