import {Component, OnInit, trigger} from '@angular/core';
import {AngularFireAuth} from "angularfire2/auth";
import {AngularFirestore} from "angularfire2/firestore";

@Component({
  selector: 'app-new-section-dialog',
  templateUrl: './new-section-dialog.component.html',
  styleUrls: ['./new-section-dialog.component.css']
})
export class NewSectionDialogComponent implements OnInit {
  courseCode: string;
  sectionNumber: string;
  year: string;
  semester: string;

  constructor(private af: AngularFireAuth, private db: AngularFirestore) {
  }

  ngOnInit() {
  }

  addSection() {
    let newSection = this.semester + "_" + this.year + "_" + this.courseCode + "_" + this.sectionNumber;
    console.log("Add " + newSection.toUpperCase() + " to user with ID :" + this.af.auth.currentUser.uid)
    this.db.collection("sections").add({userId: this.af.auth.currentUser.uid, section: newSection.toUpperCase()})
  }

}



