import {Component, OnInit} from '@angular/core';
import {AngularFireAuth} from "angularfire2/auth";
import {NgForm} from "@angular/forms";
import {AngularFirestore} from "angularfire2/firestore";


@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  auth: AngularFireAuth;
  firestore : AngularFirestore;

  constructor(auth: AngularFireAuth,firestore : AngularFirestore) {
    this.auth = auth;
    this.firestore = firestore;
  }

  ngOnInit() {
  }


  registerUser(registerForm: NgForm) {
    let email = registerForm.value.userEmail;
    let password1 = registerForm.value.userPassword1;
    let password2 = registerForm.value.userPassword2;
   /* if(!email.includes("@qu.edu.qa")){
      alert("NOT A QU EMAIL !")
    }else {*/
      if (password1 != password2) {
        alert("Please check your passwords again !")
        registerForm.resetForm({
          password1: '',
          password2: ''
        })
      } else {
        this.auth.auth.createUserWithEmailAndPassword(email, password1)
          .then(s => {
            this.auth.auth.currentUser.sendEmailVerification()
              .then(isSuccess => {
                let uid = this.auth.auth.currentUser.uid;
                let email = this.auth.auth.currentUser.email;

                this.firestore.collection('instructors').doc(uid).set({
                  userId : uid,
                  email : email
                });
                alert("Please check your email to verify your account !");
              })
              .catch(error => {
                console.log(error)
              })
          })
          .catch(error => {
            console.log(error)
          })
      }
    }





}



