import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {AngularFireModule} from 'angularfire2';
import {AngularFirestoreModule} from 'angularfire2/firestore';
import {AngularFireAuthModule} from 'angularfire2/auth';
import {environment} from '../environments/environment';

import {AppComponent} from './app.component';
import {NavbarComponent} from './navbar/navbar.component';
import {SectionsComponent} from './sections/sections.component';
import {AttendanceComponent} from './attendance/attendance.component';
import {QuizzesComponent} from './quizzes/quizzes.component';
import {LoginComponent} from './login/login.component';
import {RegisterComponent} from './register/register.component';
import {RouterModule} from "@angular/router";
import {FormsModule} from "@angular/forms";
import {UserService} from "./user.service";
import {AuthGuard} from "./auth.guard";
import {QRCodeModule} from 'angular2-qrcode';

import {
  MatAutocompleteModule,
  MatButtonModule,
  MatButtonToggleModule,
  MatCardModule,
  MatCheckboxModule,
  MatChipsModule,
  MatDatepickerModule,
  MatDialogModule,
  MatExpansionModule,
  MatGridListModule,
  MatIconModule,
  MatInputModule,
  MatListModule,
  MatMenuModule,
  MatNativeDateModule,
  MatPaginatorModule,
  MatProgressBarModule,
  MatProgressSpinnerModule,
  MatRadioModule,
  MatRippleModule,
  MatSelectModule,
  MatSidenavModule,
  MatSliderModule,
  MatSlideToggleModule,
  MatSnackBarModule,
  MatSortModule,
  MatTableModule,
  MatTabsModule,
  MatToolbarModule,
  MatTooltipModule,
  MatStepperModule,
} from '@angular/material';
import {MatFormFieldModule} from '@angular/material/form-field';
import {CdkTableModule} from '@angular/cdk/table';
import {Ng2FilterPipeModule} from 'ng2-filter-pipe';
import {searchPipe} from './app.component';
import {ResetPasswordComponent} from './reset-password/reset-password.component';
import {NewSectionDialogComponent} from './new-section-dialog/new-section-dialog.component';
import {ReactiveFormsModule} from '@angular/forms';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {FeaturesComponent} from './features/features.component';
import {QrCodeDialogComponent} from './qr-code-dialog/qr-code-dialog.component';
import { UploadFormComponent } from './uploads/upload-form/upload-form.component';
import {UploadService} from "./uploads/shared/upload.service";
import {AngularFireDatabase} from "angularfire2/database";

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    SectionsComponent,
    AttendanceComponent,
    QuizzesComponent,
    LoginComponent,
    RegisterComponent,
    searchPipe,
    ResetPasswordComponent,
    NewSectionDialogComponent,
    FeaturesComponent,
    QrCodeDialogComponent,
    UploadFormComponent],
  imports: [
    FormsModule,
    BrowserModule,
    BrowserModule,
    FormsModule,
    AngularFirestoreModule,
    AngularFireAuthModule,
    MatGridListModule,
    MatDialogModule,
    MatFormFieldModule,
    MatAutocompleteModule,
    MatButtonModule,
    MatButtonToggleModule,
    MatCardModule,
    MatCheckboxModule,
    MatChipsModule,
    MatDatepickerModule,
    MatDialogModule,
    MatExpansionModule,
    MatGridListModule,
    MatIconModule,
    MatInputModule,
    MatListModule,
    MatMenuModule,
    MatNativeDateModule,
    MatPaginatorModule,
    MatProgressBarModule,
    MatProgressSpinnerModule,
    MatRadioModule,
    MatRippleModule,
    MatSelectModule,
    MatSidenavModule,
    MatSliderModule,
    MatSlideToggleModule,
    MatSnackBarModule,
    MatSortModule,
    MatTableModule,
    MatTabsModule,
    MatToolbarModule,
    MatTooltipModule,
    MatStepperModule,
    CdkTableModule,
    Ng2FilterPipeModule,
    ReactiveFormsModule,
    BrowserAnimationsModule,
    QRCodeModule,
    AngularFireModule.initializeApp(environment.firebase),
    RouterModule.forRoot([
      {path: '', component: SectionsComponent, canActivate: [AuthGuard]},
      {path: 'login', component: LoginComponent},
      {path: 'register', component: RegisterComponent},
      {path: 'reset', component: ResetPasswordComponent},
      {path: 'sections', component: SectionsComponent, canActivate: [AuthGuard]},
      {path: 'attendance/:section', component: AttendanceComponent, canActivate: [AuthGuard]},
      {path: 'quizzes/:section', component: QuizzesComponent, canActivate: [AuthGuard]},
      {path: 'upload', component: UploadFormComponent},



    ])
  ],
  entryComponents: [NewSectionDialogComponent, FeaturesComponent, QrCodeDialogComponent],
  providers: [UserService, AuthGuard, searchPipe,UploadService,AngularFireDatabase],
  bootstrap: [AppComponent]
})
export class AppModule {
}



