import {animate, Component, OnInit, state, style, transition, trigger} from '@angular/core';
import {MatDialog, MatDialogRef} from "@angular/material";
import {NewSectionDialogComponent} from "../new-section-dialog/new-section-dialog.component";
import {FeaturesComponent} from "../features/features.component";
import {AngularFirestore, AngularFirestoreCollection} from "angularfire2/firestore";
import {Observable} from "rxjs/Observable";
import {AngularFireAuth} from "angularfire2/auth";

@Component({
  selector: 'app-sections',
  templateUrl: './sections.component.html',
  styleUrls: ['./sections.component.css']

})
export class SectionsComponent implements OnInit {
  userFilter: any
  sectionsCol : AngularFirestoreCollection<any>;
  sectionsList : Observable<any[]>;

  constructor(private dialog: MatDialog,private afs : AngularFirestore,private afa :AngularFireAuth) {
  }

  ngOnInit() {
    this.sectionsCol = this.afs.collection("sections",ref=> {
      return ref.where("userId", "==", this.afa.auth.currentUser.uid);
    });
    this.sectionsList = this.sectionsCol.valueChanges();

  }

  newSectionDialogRef: MatDialogRef<NewSectionDialogComponent>;


  openAddSectionDialog() {
    this.newSectionDialogRef = this.dialog.open(NewSectionDialogComponent, {
      hasBackdrop: true
    });
  }



  openFeaturesDialog(section: string) {
    let selectFeatureDialogRef = this.dialog.open(FeaturesComponent, {
      hasBackdrop: true,
    });
    selectFeatureDialogRef.componentInstance.section = section;

  }


}



