import {Component, OnInit} from '@angular/core';
import {AngularFirestore} from "angularfire2/firestore";

@Component({
  selector: 'app-qr-code-dialog',
  templateUrl: './qr-code-dialog.component.html',
  styleUrls: ['./qr-code-dialog.component.css']
})
export class QrCodeDialogComponent implements OnInit {
  section : string;
  constructor(private afs : AngularFirestore) {
  }
  generating : boolean =false;
  stop : boolean = false;
  qrCode: string;
  i: number = 0;

  ngOnInit() {
    this.generateQRCode();


  }

  stopGenerating(){
    this.stop = true;
    this.generating = false;
    //to remove the last qrCode
    this.afs.collection("qrCodes").doc(this.qrCode).delete();

  }


  generateQRCode() {
    this.generating = true;
    this.afs.collection("qrCodes").add({section:this.section}).then(data=>{
      this.qrCode= data.id;
      console.log(this.i+" :" + this.qrCode)
      this.i++;
    });

    setInterval(() => {
      if(this.stop!=true){
        this.afs.collection("qrCodes").doc(this.qrCode).delete();
        this.afs.collection("qrCodes").add({section:this.section}).then(data=>{
          this.qrCode= data.id;
          console.log(this.i+" :" + this.qrCode)
          this.i++;


        })

      }else return;

    }, 10000);

  }


}



