import {Component, OnInit} from '@angular/core';
import {ActivatedRoute} from "@angular/router";
import {MatDatepickerInputEvent, MatDialog, MatDialogRef} from "@angular/material";
import {QrCodeDialogComponent} from "../qr-code-dialog/qr-code-dialog.component";
import {AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument} from "angularfire2/firestore";
import {Observable} from "rxjs/Observable";
import {FormControl} from "@angular/forms";

@Component({
  selector: 'app-attendance',
  templateUrl: './attendance.component.html',
  styleUrls: ['./attendance.component.css']
})


export class AttendanceComponent implements OnInit {
  currentSection: string;
  studentsCol: AngularFirestoreCollection<any>;
  studentsList: Observable<any[]>;
  studentID :string;
  date:string
  studentsRecordsCol :AngularFirestoreCollection<any>;
  studentsRecord : Observable<any[]>;
  date2 :  FormControl;
  serializedDate = new FormControl((new Date()).toISOString());
  constructor(private route: ActivatedRoute, private dialog: MatDialog, private afs: AngularFirestore) {

  }

  displayQRCode() {

    if (!this.dialog.openDialogs.length) {
      let qrCodeDialogRef = this.dialog.open(QrCodeDialogComponent, {
        hasBackdrop: false,
      });
      qrCodeDialogRef.componentInstance.section = this.currentSection;


    }

  }


  ngOnInit() {
    this.route.params.subscribe(params => {
      this.currentSection = params.section
    });



  }

  searchByID(){
    this.studentsList = null;
    this.studentsRecordsCol = this.afs.collection("Attendance",ref=>{
      return ref.where("section","==",this.currentSection).where("studentID","==",this.studentID+"");
    });
    this.studentsRecord = this.studentsRecordsCol.valueChanges();
  }


  searchByDate(){

    this.studentsRecord=null;
    this.studentsCol = this.afs.collection("Attendance",ref=>{
      return ref.where("section","==",this.currentSection).where("date","==",this.date+"");
    });
    this.studentsList = this.studentsCol.valueChanges();
  }


}



